import logo from "./logo.svg";
import "./App.css";
import Button from "@mui/material/Button";
import Popover from "./components/Popover";
import Filter from "./components/Filter";
import Table from "./components/Table";
import axios from "axios";
import Select from "@mui/material/Select";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Box from "@mui/material/Box";
import { useEffect, useState } from "react";

function App(props) {
  const [d, set_d] = useState([]);
  const [hasFilter, setHasFilter] = useState(false);
  const [filters, setFilters] = useState({});
  const [page, setPage] = useState(1);
  const [lastItemCreatedAt, setLastItemCreatedAt] = useState(null);
  let endpoints = [
    {
      name: "Staging",
      url: "https://versa-tool-mqtt-dev-svc.azurewebsites.net",
      sas: "?sv=2020-08-04&si=meraki-snapshots-staging-17FB47BD0A6&sr=c&sig=dv9v0yX3SkeD%2FVHemxWYN8DVfV9ctOgSdgRtMyKGZBI%3D",
    },
    // {
    //   name: "Pre-production",
    //   url: "https://viana-internal-snapper-ppd-svc.azurewebsites.net",
    //   sas: "?sv=2020-08-04&si=meraki-snapshots-ppd-17FB49E50A9&sr=c&sig=JH4rEAqzPHB8%2BGGI%2FZH7efOQLCHCnURTOGEQG9w%2F1m4%3D",
    // },
    // {
    //   name: "Production",
    //   url: "http://localhost:8080",
    //   sas: "?sv=2020-08-04&si=meraki-snapshots-production-1801178C512&sr=c&sig=LFt5kF6YwXjLOjCL4d2DpHDdlQw2p0PWS5CxqikmD4s%3D",
    // },
  ];

  const [env, setEnv] = useState(endpoints[0]);

  useEffect(() => {
    axios
      .get(`${env.url}/detections/1`)
      .then((res) => {
        if (res.data.detections.length > 1) {
          // setLastItemCreatedAt(moment(res.data.detections[0].createdAt).subtract(8, 'hours'));
          setLastItemCreatedAt(res.data.detections[0].timestamp_str);
        }

        set_d(res.data.detections);
      })
      .catch((err) => {
        console.log("err", err);
      });
  }, [env]);

  const goFilter = (filters) => {
    setFilters(filters);
    axios
      .get(`${env.url}/detections_filter/1`, { params: { ...filters } })
      .then((res) => {
        if (res.data.detections.length > 1) {
          setLastItemCreatedAt(res.data.detections[0].timestamp_str);
        }
        set_d(res.data.detections);
        setHasFilter(true);

        setTimeout(() => {
          if (res.data.detections.length === 0) {
            alert("Ooops! :P No Result");
          }
        }, 100);
      })
      .catch((err) => {
        console.log("err", err);
      });
  };
  // https://viana-internal-snapper-staging-svc.azurewebsites.net
  const loadMore = (prev) => {
    if (hasFilter) {
      axios
        .get(`${env.url}/detections_filter/${page + 1}`, {
          params: { ...filters },
          lastItemCreatedAt,
        })
        .then((res) => {
          setPage(page + 1);
          set_d([...prev, ...res.data.detections]);
        })
        .catch((err) => {
          console.log("err", err);
        });
    } else {
      axios
        .get(`${env.url}/detections/${page + 1}`, {
          params: { lastItemCreatedAt },
        })
        .then((res) => {
          setPage(page + 1);
          set_d([...prev, ...res.data.detections]);
        })
        .catch((err) => {
          console.log("err", err);
        });
    }
  };

  return (
    <div>
      <link
        rel="stylesheet"
        href="https://fonts.googleapis.com/icon?family=Material+Icons"
      />
      <body>
        <div>
          <div style={{ display: "flex", justifyContent: "space-between" }}>
            <Filter
              switches={() => {
                props.switches();
              }}
              goFilter={(d) => goFilter(d)}
            />
            <FormControl sx={{ m: 1, minWidth: 80 }}>
              <InputLabel id="demo-simple-select-autowidth-label">
                ENV
              </InputLabel>
              <Select
                labelId="demo-simple-select-autowidth-label"
                id="demo-simple-select-autowidth"
                value={env.name}
                onChange={(e) =>
                  setEnv(endpoints.find((d) => d.name === e.target.value))
                }
                autoWidth
                label="Age"
              >
                <MenuItem value={endpoints[0].name}>Staging</MenuItem>
              </Select>
            </FormControl>
          </div>
          <Table env={env} detectionz={d} loadMore={(prev) => loadMore(prev)} />
        </div>
      </body>
    </div>
  );
}

export default App;
