import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell, { tableCellClasses } from '@mui/material/TableCell';
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import { styled } from '@mui/material/styles';
import axios from "axios";
import { useEffect, useState } from "react";
import ImageMapper from "react-image-mapper";
import Button from "@mui/material/Button";
import Popover from "@mui/material/Popover";
import moment from "moment";

const StyledTableCell = styled(TableCell)(({ theme }) => ({
  [`&.${tableCellClasses.head}`]: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  [`&.${tableCellClasses.body}`]: {
    fontSize: 14,
  },
}));

const StyledTableRow = styled(TableRow)(({ theme }) => ({
  '&:nth-of-type(odd)': {
    backgroundColor: theme.palette.action.hover,
  },
  // hide last border
  '&:last-child td, &:last-child th': {
    border: 0,
  },
}));

export default function BasicTable(props) {
  let [detections, setDetections] = useState(props.detectionz);
  useEffect(() => {
    setDetections(props.detectionz);
  }, [props.detectionz]);

  return (
    <TableContainer component={Paper}>
      <Table sx={{ minWidth: 650 }} aria-label="simple table">
        <TableHead>
          <TableRow>
            <StyledTableCell style={{ fontFamily: 'Nunito', fontWeight: 'bold' }} align="left">INDEX</StyledTableCell>
            <StyledTableCell style={{ fontFamily: 'Nunito', fontWeight: 'bold' }} align="left">ID</StyledTableCell>
            <StyledTableCell style={{ fontFamily: 'Nunito', fontWeight: 'bold' }} align="center">SERIAL ID</StyledTableCell>
            <StyledTableCell style={{ fontFamily: 'Nunito', fontWeight: 'bold' }} align="left">DETAILS</StyledTableCell>
            <StyledTableCell style={{ fontFamily: 'Nunito', fontWeight: 'bold' }} align="left">VEHICLE ATTRIBUTES</StyledTableCell>
            <StyledTableCell style={{ fontFamily: 'Nunito', fontWeight: 'bold' }} align="right">IMAGE</StyledTableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {detections &&
            detections.map((row, index) => {
              return (
                <TableRowz
                  env={props.env}
                  key={`${row.id}`}
                  row={row}
                  index={index}
                />
              )
            })}
        </TableBody>
        {detections.length > 0 &&
          <Button
            variant="outlined"
            style={{ height: 55.96, fontFamily: 'Nunito', fontWeight: 'bold', marginLeft: 10, marginBottom: 10, marginTop: 10 }}
            onClick={() => {
              props.loadMore(detections);
            }}
          >
            {" "}
            LOAD MORE
          </Button>
        }

      </Table>
    </TableContainer>
  );
}



const TableRowz = (props) => {
  let row = props.row;
  let lpr = row.lpr_result;
  let details = lpr ? lpr.detection ? lpr.detection : false : false;
  let crop_object = props.row.payload?.crop_object;
  const [anchorEl, setAnchorEl] = useState(null);
  const handlePopoverOpen = (event) => {
    setAnchorEl(event.currentTarget);
  };
  console.log(row);
  const handlePopoverClose = () => {
    setAnchorEl(null);
  };
  console.log(moment(parseInt(row.timestamp_str)).toISOString())
  const open = Boolean(anchorEl);
  return (
    <StyledTableRow
      style={{
        backgroundColor: row.track_object.is_test ? '#660000' : ''
      }}
      key={props.key}
      sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
    >
      <StyledTableCell style={{ fontFamily: 'Nunito', fontWeight: 'bold' }} align="left">{props.index + 1}</StyledTableCell>
      <StyledTableCell style={{ fontFamily: 'Nunito', fontWeight: 'bold' }} align="left">{row.id}</StyledTableCell>
      <StyledTableCell style={{ fontFamily: 'Nunito', fontWeight: 'bold' }} align="center">{row.serial_id}</StyledTableCell>
      <StyledTableCell style={{ paddingBottom: '0px', fontFamily: 'Nunito', fontWeight: 'bold' }} align="left">
        <p>
          <span style={{ fontWeight: 'bold' }}>SERIAL ID: </span>
          <span style={{ fontWeight: 'normal' }}>{row.serial_id}</span>
        </p>
        <p>
          <span style={{ fontWeight: 'bold' }}>PH -DETECTION DATE: </span>
          {/* <span style={{ fontWeight: 'normal' }}>{new Date(parseInt(row.timestamp_str)).toISOString()}</span> */}
          <span style={{ fontWeight: 'normal' }}>{moment(parseInt(row.timestamp_str)).add(8, 'hours').toISOString().replace('Z', ' ').replace('T', ' ')}</span>
        </p>
        <p>
          <span style={{ fontWeight: 'bold' }}>EDT DETECTION DATE: </span>
          <span style={{ fontWeight: 'normal' }}>{moment(parseInt(row.timestamp_str)).subtract(4, 'hours').toISOString().replace('Z', ' ').replace('T', ' ')}</span>
        </p>
        <p>

          {row.track_object.map((output, index) =>
            <div style={{ paddingTop: 5, paddingBottom: 5 }}>
              <p style={{ margin: 0 }}>
                <span style={{ fontWeight: 'normal' }}>{output.location.join(' / ')} - {output.score * 100}% - {output.class === 1 ? 'HAND' : 'BODY'}</span>
              </p>
              {row.track_object.length > 1 && row.track_object[row.track_object.length] === index &&
                <p>
                  --------------------------------
                </p>
              }
            </div>
          )}
        </p>
      </StyledTableCell>
      <StyledTableCell style={{ fontFamily: 'Nunito', fontWeight: 'bold' }}>
        <div>
          {details ?
            <div>
              <p>
                <span style={{ fontWeight: 'bold' }}>COLOR: </span>
                <span style={{ fontWeight: 'normal' }}>{details.color}</span>
              </p>

              <p>
                <span style={{ fontWeight: 'bold' }}>TYPE: </span>
                <span style={{ fontWeight: 'normal' }}>{details.type}</span>
              </p>

              <p>
                <span style={{ fontWeight: 'bold' }}>MAKE: </span>
                <span style={{ fontWeight: 'normal' }}>{details.make}</span>
              </p>

              <p>
                <span style={{ fontWeight: 'bold' }}>MODEL: </span>
                <span style={{ fontWeight: 'normal' }}>{details.model}</span>
              </p>

              <p>
                <span style={{ fontWeight: 'bold' }}>CONFIDENCE: </span>
                <span style={{ fontWeight: 'normal' }}>{details.confidence * 100}%</span>
              </p>
            </div>
            :
            <div>
              <p>No car attributes</p>
            </div>
          }
        </div>
      </StyledTableCell>
      <StyledTableCell
        // onMouseEnter={handlePopoverOpen}
        onClick={handlePopoverOpen}
        onMouseLeave={handlePopoverClose}
        align="right"
        style={{ display: "flex", justifyContent: "end", alignItems: "end", zIndex: 1000 }}
      >
        <div
          aria-owns={open ? "mouse-over-popover" : undefined}
          aria-haspopup="true"
        >
          <ImageMapper
            src={`${row.image_url}`}
            active={false}
            map={{
              name: `${row.timestamp}`,
              areas: row.track_object.map(output => ({
                name: `1-${row.timestamp}`,
                shape: "rect",
                coords: [
                  output.location[0] * 500,
                  output.location[1] * 281,
                  output.location[2] * 500,
                  output.location[3] * 281,
                ],
                strokeColor: output.class === 1 ? "rgba(0, 230, 64, 1)" : "rgba(242, 38, 19,1)",
                preFillColor: "rgba(76, 175, 80, 0.1)",
                lineWidth: 2,
                disabled: true
              }))
            }}
            width={500}
          />
        </div>
        <Popover
          style={{ backgroundColor: "transparent" }}
          id="mouse-over-popover"
          sx={{
            pointerEvents: "none",
          }}
          open={open}
          anchorEl={anchorEl}
          anchorReference="anchorPosition"
          anchorPosition={{
            top: window.innerHeight / 2,
            left: window.innerWidth / 5,
          }}
          anchorOrigin={{
            vertical: "center",
            horizontal: "center",
          }}
          transformOrigin={{
            vertical: "center",
            horizontal: "center",
          }}
          onClose={handlePopoverClose}
          disableRestoreFocus
        >
          <ImageMapper
            src={`${row.image_url}`}
            width={1920}
            active={false}
            map={{
              name: `${row.timestamp}`,
              areas: row.track_object.map(output => ({
                name: `1-${row.timestamp}`,
                shape: "rect",
                coords: [
                  output.location[0] * 1920,
                  output.location[1] * 1080,
                  output.location[2] * 1920,
                  output.location[3] * 1080,
                ],
                strokeColor: output.class === 1 ? "rgba(0, 230, 64, 1)" : "rgba(242, 38, 19,1)",
                preFillColor: "rgba(76, 175, 80, 0.1)",
                lineWidth: 2,
                disabled: true
              }))
            }}
          />
        </Popover>
      </StyledTableCell>
    </StyledTableRow>
  );
};

