import * as React from "react";
import Box from "@mui/material/Box";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";
import DateTimePicker from "react-datetime-picker";
import { useEffect, useState } from "react";
import Popover from "@mui/material/Popover";
import Alert from "@mui/material/Alert";
import Typography from "@mui/material/Typography";
import moment from "moment";

export default function BasicTextFields(props) {
  const [OID, setOID] = useState(null);
  const [serial_ID, set_serial_ID] = useState(null);
  const [dateValue, onChange] = useState(null);
  const [anchorEl, setAnchorEl] = React.useState(null);

  // const handleClick = (event) => {
  //   setAnchorEl(event.currentTarget);
  // };

  // const handleClose = () => {
  //   setAnchorEl(null);
  // };

  const open = Boolean(anchorEl);
  const id = open ? "simple-popover" : undefined;
  return (
    <Box
      component="form"
      sx={{
        "& > :not(style)": { m: 1, width: "25ch" },
      }}
      noValidates
      autoComplete="off"
    >
      <TextField
        inputProps={{ fontFamily: "Nunito", fontWeight: "bold" }} // font size of input text
        InputLabelProps={{ fontFamily: "Nunito", fontWeight: "bold" }}
        onChange={(event) => {
          set_serial_ID(event.target.value);
        }}
        id="outlined-basic"
        label="Filter by serial id"
        variant="outlined"
      />
      {/* <TextField
        inputProps={{ fontFamily: "Nunito", fontWeight: "bold" }} // font size of input text
        InputLabelProps={{ fontFamily: "Nunito", fontWeight: "bold" }}
        onChange={(event) => {
          setOID(event.target.value);
        }}
        id="outlined-basic"
        label="Filter by oid"
        variant="outlined"
      /> */}

      <span>
        <DateTimePicker
          style={{ margin: 100 }}
          onChange={onChange}
          value={dateValue}
        />
      </span>

      {/* <span>
        <span onClick={handleClick} aria-describedby={id} className="dot">
          i
        </span>
      </span> */}
      {/* <Popover
        id={id}
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
      >
        <Typography sx={{ p: 2 }}>
          <p>Selected date is expected to be EDT</p>
          <p style={{ marginTop: 0 }}>Double check AM/PM</p>
        </Typography>
      </Popover> */}
      <Button
        variant="outlined"
        style={{ height: 60, fontFamily: "Nunito", fontWeight: "bold" }}
        onClick={() => {
          props.goFilter(
            {
              playback: false,
              oid: OID,
              serial_id: serial_ID,
              EDTdate: moment(dateValue).add(12, "hours").toISOString(),
            },
            1
          );
        }}
      >
        GO
      </Button>
    </Box>
  );
}
